*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

button {
  border: none;
  cursor: pointer;
}

button,
input,
label {
  font: inherit;
}

a {
  font-weight: 500;
  color: var(--color-soft-alternate);
  text-decoration: inherit;
}
a:hover {
  text-decoration: underline;
}

samp {
  font-family: SFMono-Regular, Menlo, Monaco, Consolas, monospace;
  font-size: 1em;
}

body {
  -webkit-touch-action: none; 
  touch-action: none;

  position: relative !important;
  padding: 0;
  overflow: hidden;
  margin: 0;
  display: flex;
  place-items: center;
  height: 100svh;
}

html {
  virtual-keyboard: overlays-content;
  line-height: 1.5;
  -webkit-text-size-adjust: 100%;
  font-family: system-ui, sans-serif;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
   -webkit-touch-action: none; 
  touch-action: none;
  background-color: var(--color-map-layout-fill);
}

:root {
  --color-system-black: #000000;
  --color-system-white: #ffffff;
  --color-system-blue: #007aff;
  --color-system-gray-03: #7f7f7f;

  --color-map-search-scope: #FF766F;
  --color-map-green-selected: #4f9661;
  --color-map-green-dark: #61a166;
  --color-map-green-icon: #34c759;
  --color-map-green-default: #71d88b;
  --color-map-blue-default: #4da2ff;

  --chakra-color-gray-300: #cbd5e0;
  --chakra-color-gray-100: #edf2f7;

  --color-bg-primary-light: #f2f2f7;
  --color-bg-primary-dark: #313655;

  --color-chakra-white: #edf2f7;
  --color-chakra-black: #242a36;

  --color-bg-secondary-light: var(--color-system-white);
  --color-bg-secondary-dark: #515780;

  --color-floor-option-bg-opacity-light: #4d525c;
  --color-floor-option-bg-opacity-dark: #c8cdd4;

  --color-bg-select-light: #242a36cf;
  --color-bg-select-dark: #edf2f7d3;

  --color-input-placeholder-light: #edf2f7a6;
  --color-input-placeholder-dark: #242a366a;

  --color-map-layout-background-light: #4e4e4eff;
  --color-map-layout-background-dark: #363636ff;

  --color-map-layout-stroke: var(--color-system-gray-03);

  --color-map-layout-fill-light: var(--color-system-white);
  --color-map-layout-fill-dark: #1a202c;

  --dropshadow-map-layout-fancy-on: drop-shadow(2px 2px 4px rgba(0, 0, 0, 0.5));
  --dropshadow-map-layout-fancy-off: "";

  --background-filter-fancy-on: blur(5px);
  --background-filter-fancy-off: "";

  --transition-drawer-fancy-on: left 0.4s ease;
  --transition-drawer-fancy-off: "";

  --transition-drawer-overlay-fancy-on: all ease 0.4s;
  --transition-drawer-overlay-fancy-off: "";

  --color-drawer-bg-light: var(--color-system-white);
  --color-drawer-bg-dark: #2d3748;

  --color-button-bg-dark: rgba(255, 255, 255, 0.08);
  --color-button-bg-light: var(--chakra-color-gray-100);

  --color-button-bg-active-dark: rgba(255, 255, 255, 0.24);
  --color-button-bg-active-light: var(--chakra-color-gray-300);

  --color-button-bg-hover-dark: rgba(255, 255, 255, 0.37);
  --color-button-bg-hover-light: var(--chakra-color-gray-300);

  --margin-top: 19px;
}

html[data-theme="dark"] {
  --color-bg-select: var(--color-bg-select-dark);
  --color-input-placeholder: var(--color-input-placeholder-dark);
  --color-floor-option-bg-opacity: var(--color-floor-option-bg-opacity-dark);
  --color-map-layout-background: var(--color-map-layout-background-dark);
  --color-map-layout-fill: var(--color-map-layout-fill-dark);
  --color-drawer-bg: var(--color-drawer-bg-dark);
  --color-button-bg: var(--color-button-bg-dark);
  --color-button-bg-active: var(--color-button-bg-active-dark);
  --color-button-bg-hover: var(--color-button-bg-hover-dark);
  --color-soft: var(--color-chakra-black);
  --color-soft-alternate: var(--color-chakra-white);
  color-scheme: dark;
}

html[data-theme="light"] {
  --color-floor-option-bg-opacity: var(--color-floor-option-bg-opacity-light);
  --color-bg-select: var(--color-bg-select-light);
  --color-input-placeholder: var(--color-input-placeholder-light);
  --color-map-layout-background: var(--color-map-layout-background-light);
  --color-map-layout-fill: var(--color-map-layout-fill-light);
  --color-drawer-bg: var(--color-drawer-bg-light);
  --color-button-bg: var(--color-button-bg-light);
  --color-button-bg-active: var(--color-button-bg-active-light);
  --color-button-bg-hover: var(--color-button-bg-hover-light);
  --color-soft: var(--color-chakra-white);
  --color-soft-alternate: var(--color-chakra-black);
  color-scheme: light;
}

html[data-fancy="true"] {
  --colorMapLayoutdropShadowFancy: var(--dropshadow-map-layout-fancy-on);
  --background-filter-fancy: var(--background-filter-fancy-on);
  --transition-drawer-fancy: var(--transition-drawer-fancy-on);
  --transition-drawer-overlay-fancy: var(--transition-drawer-overlay-fancy-on);
}

html[data-fancy="false"] {
  --colorMapLayoutdropShadowFancy: var(--dropshadow-map-layout-fancy-off);
  --background-filter-fancy: var(--background-filter-fancy-off);
  --transition-drawer-fancy: var(--transition-drawer-fancy-off);
  --transition-drawer-overlay-fancy: var(--transition-drawer-overlay-fancy-off);
}

.floor-options {
  touch-action: none;
  display: flex;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  flex-direction: column;
  z-index: 2;
  position: absolute;
  justify-content: space-between;
  align-items: center;
  top: 50%;
  transform: translate(-50%, -50%);
  right: 0px;
  background: var(--color-soft-alternate);
  border-radius: 20px;
  padding: 2.5px;
  width: 35px;
  height: 95px;
}

.sma {
  width: inherit;
}

.floor-option__button {
  touch-action: none;
  background: var(--color-soft-alternate);
  color: var(--color-soft);
  border-radius: 15px;
  width: 30px;
  height: 30px;
  display: flex;
  font-size: 1rem;
  font-weight: 600;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 0;
  border: none;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}

.floor-option__button_current-floor {
  touch-action: none;
  background: var(--color-soft);
  color: var(--color-soft-alternate);
