.chakra-search__icon {
  width: 1em;
  height: 1em;
  display: inline-block;
  line-height: 1em;
  flex-shrink: 0;
  color: rgb(36, 42, 54);
  vertical-align: middle;
}

.chakra-search__icon path {
  fill: var(--color-soft);
}

.search {
  touch-action: none;
  display: flex;
  align-items: center;
  top: var(--margin-top);
  position: absolute;
  z-index: 2;
  width: 100%;
  justify-content: center;
}

.search-select {
  border-radius: 9px 0 0 9px;
  background: var(--color-bg-select);
  color: var(--color-soft);
}

.search-input {
  touch-action: none;
  border-radius: 0 9px 9px 0;
  background: var(--color-bg-select);
  color: var(--color-soft);
  width: auto;
  border-width: 1px;
  border-style: solid;
  border-image: initial;
  border-color: inherit;
  min-width: 0px;
  outline: transparent solid 2px;
  outline-offset: 2px;
  position: relative;
  appearance: none;
}

.input-group {
  display: flex;
  width: 180px;
}

.drawer {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 3;
  left: -300px;
  top: 0;
  bottom: 0;
  height: 100%;
  width: 300px;
  background-color: var(--color-drawer-bg);
  overflow-y: auto;
  transition: var(--transition-drawer-fancy-on);
}

#drawer-state {
  display: none;
}

#drawer-state:checked ~ .drawer {
  left: 0;
}

.drawer__overlay {
  -webkit-touch-action: none; 
  touch-action: none;

  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  -webkit-backdrop-filter: var(--background-filter-fancy);
  backdrop-filter: var(--background-filter-fancy);
  background-color: #00000088;
  opacity: 0;
  visibility: hidden;
  transition: var(--transition-drawer-overlay-fancy-on);
}

#drawer-state:checked ~ .drawer__overlay {
  opacity: 1;
  visibility: visible;
}

.drawer__open-button {
  touch-action: none;
  z-index: 2;
  left: 0px;
  top: var(--margin-top);
  height: 40px;
  min-width: 1.5rem !important;
  width: 1.5rem !important;
  padding-inline-start: 0rem !important;
  background: var(--color-soft-alternate) !important;
  -webkit-padding-end: 0rem !important;
  padding-inline-end: 0rem !important;
  padding: 0px !important;
  border-top-right-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
  border-top-left-radius: 0rem !important;
  border-bottom-left-radius: 0rem !important;
  position: absolute !important;
}

.drawer__close-button {
  touch-action: none;
  border-top-left-radius: 0.5rem;
  border-bottom-left-radius: 0.5rem;
  border-top-right-radius: 0rem !important;
  border-bottom-right-radius: 0rem !important;
  background-color: var(--color-soft-alternate) !important;
  right: -24px;
  height: 40px !important;
  width: 80px !important;
}

.drawer__header {
  touch-action: none;
  display: flex;
  flex: 0 1 0%;
  padding-inline-start: 1.5rem;
  padding-inline-end: 1.5rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  font-size: 1.25rem;
  font-weight: 600;
  border-bottom-width: 1px;
  border-bottom: 1px solid var(--color-button-bg);
