}

.drawer__body {
  touch-action: none;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding-inline-start: 1.5rem;
  padding-inline-end: 1.5rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  gap: 0.75rem;
}

.drawer__footer {
  touch-action: none;
  display: flex;
  flex: 2;
  flex-direction: column;
  justify-content: flex-end;
  padding-inline-start: 1.5rem;
  padding-inline-end: 1.5rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.drawer__footer_mt_10 {
