input[type="checkbox"].fancy-toggle__switch {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  width: 3.53rem;
  height: 1.765rem;
  border-radius: 1.765rem;
  background-color: var(--color-button-bg-active);
  outline: 0;
  cursor: pointer;
  transition: background-color 0.09s ease-in-out;
  position: relative;
}

input[type="checkbox"].fancy-toggle__switch:checked {
  background-color: var(--color-map-blue-default);
}

input[type="checkbox"].fancy-toggle__switch::after {
  content: "";
  width: 1.765rem;
  height: 1.765rem;
  background-color: var(--color-system-white);
  border-radius: 1.765rem;
  position: absolute;
  transform: scale(0.85);
  left: 0;
  transition: left 0.09s ease-in-out;
  box-shadow: 0 0.1em rgba(0, 0, 0, 0.5);
}

input[type="checkbox"].fancy-toggle__switch:checked::after {
  left: 1.765rem;
}

